
.accordionDiv {
    max-width: 80%;
    margin: 2rem auto;
}
.topFaqSection{
    display: flex;
    flex-wrap: nowrap;
    margin: 5rem 1rem 0rem;
    line-height: 25px;
    z-index: 999;
    justify-content: space-between;
}
.faqTitle{
    color: #685dc5;
    font-style: normal;
}
select {
  -webkit-appearance: auto;
    -moz-appearance : auto;
  border-radius: 2rem;
  border: 1px solid #DADADA;
  padding:5px 15px;
  background: transparent;
}
select:active,select:focus{
  outline: none;
  border-radius: 2rem 2rem 0 0;
  transition: 0.1;
}
select option {
  margin: 40px;
  background: rgba(255, 255, 255, 0);
  color:#685dc5;
  font-weight: 300;
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.4);
  border-radius: 0 0 2rem 2rem;
}
.dropdownStyle{
  font-size:16px;
  color: #685DC5;
  font-weight: 500;
  box-sizing: border-box;
  border-radius: 2rem;
  height: 58px;
  background: transparent;
  width: 235px;
}
  
  
  .accordion-title {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    cursor: pointer;
    background-color: white;
    border-top: 1px solid #dadada;
  }
  
  .accordion-title,
  .accordion-content {
    padding: 1rem;
  }
  
  .accordion-content {
    margin-left: 25rem;
  }
  .custom-select-wrapper {
    position: relative;
    user-select: none;
    width: 100%;
}
.custom-select {
    position: relative;
    display: flex;
    flex-direction: column;
    border-width: 0 2px 0 2px;
    border-style: solid;
    border-color: #394a6d;
}
.custom-select__trigger {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 22px;
    font-size: 20px;
    font-weight: 300;
    color: #3b3b3b;
    height: 60px;
    line-height: 60px;
    background: #ffffff;
    cursor: pointer;
    border-width: 2px 0 2px 0;
    border-style: solid;
    border-color: #394a6d;
}
.custom-options {
    position: absolute;
    display: block;
    top: 100%;
    left: 0;
    right: 0;
    border: 2px solid #394a6d;
    border-top: 0;
    background: #fff;
    transition: all 0.5s;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    z-index: 2;
}
.custom-select.open .custom-options {
    opacity: 1;
    visibility: visible;
    pointer-events: all;
}
.custom-option {
    position: relative;
    display: block;
    padding: 0 22px 0 22px;
    font-size: 22px;
    font-weight: 300;
    color: #3b3b3b;
    line-height: 60px;
    cursor: pointer;
    transition: all 0.5s;
}
.custom-option:hover {
    cursor: pointer;
    background-color: #b2b2b2;
}
.custom-option.selected {
    color: #ffffff;
    background-color: #305c91;
}

  
  @media screen and (max-width: 700px) {
    body {
      font-size: 18px;
    }
  
    .accordion {
      width: 90%;
    }
  }